        transform: translateX(-100%);
        top: -1px;
        left: -1px;
        width: 269px;
        height: 73px;
        background-color: white;
        transition: all .5s ease-in;

    }

    p {

        font-family: "Proxima";
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 175.9%;
        /* or 42px */
        text-decoration: none;
        text-align: center;
        letter-spacing: 0.05em;
        mix-blend-mode: difference;

        color: #FFFFFF;

    }


}