@import "./../../assets//style/module/breakpoints.scss";


.no-scroll {
    overflow: hidden;
}

.main-menu {

    width: 100vw;
    height: 100vh;
    display: none;


    .menu-secondary-layer {
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        position: fixed;
        background-color: #85C3E6;
        width: 100%;
        height: 100%;
        z-index: 5;
        opacity: 0;
    }

    .menu-layer {
        z-index: 6;
        width: 100%;
        height: 100%;
        background: radial-gradient(80.27% 180.6% at 9.33% 71.2%, #0D0D0D 0%, #041D2C 100%);
        opacity: 0;
        position: relative;
        display: flex;
        align-items: center;
        justify-self: center;
        overflow: hidden;

        .menu-container {
            width: 1280px;
            height: 720px;
            margin: 0 auto;
            display: flex;
            flex-flow: column wrap;
            justify-content: space-between;
            align-items: space-between;

            .inner-menu-logo {
                text-align: center;

                .menu-logo {
                    transform: rotateX(135deg);
                }
            }

            .menu-links {
                nav {
                    ul {
                        li {
                            list-style: none;
                            font-size: 6rem;
                            font-weight: 700;
                            cursor: pointer;
                            height: 54px;
                            overflow: hidden;
                            position: relative;

                            a {
                                font-family: "Proxima";
                                font-style: normal;
                                font-weight: bold;
                                font-size: 34px;
                                line-height: 175.9%;
                                /* or 60px */
                                text-transform: uppercase;
                                text-align: center;
                                letter-spacing: 0.05em;
                                text-decoration: none;
                                color: #FFFFFF;
                                position: absolute;

                                &:hover {
                                    color: #85C3E6;
                                }

                            }
                        }
                    }
                }
            }

        }
    }

}

.button-techspace{
    .line{
        width: 200px;
    }
        a{
            button{
                width: 200px;
                height: 60px;
            }
        }
    }
        

.menu {
    z-index: 11;
}

.logo-min {
    margin-right: 15px;

    @include media('<=tablet') {
        display: none;


    }
}